

























































































import Vue from "vue";
import axios from "axios";

export default Vue.extend({
  name: "HelloWorld",

  data: () => ({
    valid: false,
    firstname: "",
    lastname: "",
    nameRules: [
      (v: string) => !!v || "Name is required",
      (v: string) => v.length <= 10 || "Name must be less than 10 characters",
    ],
    email: "",
    emailRules: [
      (v: string) => !!v || "E-mail is required",
      (v: string) => /.+@.+/.test(v) || "E-mail must be valid",
    ],
    tel: "",
    telRules: [
      (v: string) => !!v || "Phone number is required",
      (v: string) => v.length === 8,
      (v: string) => Number(v) > 0,
    ],
    streetName: "",
    streetNr: "",
    city: "",
    postCode: "",
    phoneType: "",
    problem: "",
    statusOfDevice: false,
  }),
  async created() {
    this.getCustomers();
  },
  methods: {
    async createNewCustomer() {
      try {
        const customer = await axios.post("api/customer", {
          firstName: this.firstname,
          lastName: this.lastname,
          tel: this.tel,
          streetName: this.streetName,
          streetNr: this.streetNr,
          city: this.city,
          postCode: this.postCode,
          phoneType: this.phoneType,
          problem: this.problem,
          status: false,
        });
        console.log(customer);
      } catch (err) {
        console.log(err);
      }
    },
    async getCustomers(){
      const customers= await axios.get('api/customer');
      console.log(customers);
    }
  },
});
